.groceries-list ul {
    // background-color: aquamarine;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding:15px;
}

.grocery-preview {
    list-style: none;
    display: flex;
    align-items: end;
    gap: 20px;
    padding: 5px 0;
    border-bottom: 1px solid rgba(219, 216, 216, 0.74);
    img {
        height: 150px;
        width: 150px;
        object-fit: cover;
    }
}